function PrivacyPolicy(props){
    return(
        <div>
        <TopbarMargin/>
        <BaseMargin/>
        <section style={{color:"black", backgroundColor:"white", whiteSpace:"pre-line"}}>
            <p color="white">
                {text}
            </p>
        </section>
        </div>
    );
}

export default PrivacyPolicy;