import React from 'react';
import { Row, Column } from './Grid';
import Discord from '../assets/discord_logo.png';
import Twitter from '../assets/twitter_logo.png';
import Email from '../assets/envelope.png';

var footerStyle = {
    color: "gray",
    fontSize: "15px",
    textDecoration: "underline",
    margin: "10px",
    padding: "0"
}

function Footer(){
    return(
        <div>
            <Row style={{height: "100px"}}>
                <Column centered="true">
                    <p>
                    <a target="_blank" style={footerStyle} href="/privacypolicy">Privacy Policy</a>
                    <a target="_blank" style={footerStyle} href="https://www.twitter.com/scrspc">Twitter</a>{"   "}
                    <a target="_blank" style={footerStyle} href="https://invite.gg/scorespace">Discord</a> {"  "}
                    <a target="_blank" style={footerStyle} href="mailto:info@scorespace.net">Email</a>
                    </p>
                </Column>
            </Row>
        </div>
        
    )
}

export default Footer;